import React, { Component } from "react";

export class Projects extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="container-custom">
          <div className="row h-100 w-100">
            <div className="col-10 offset-1 col-sm-10 offset-1 col-md-6 offset-md-3 my-auto">

                <h1 style={projectHeader}>My Projects</h1>
                <p style={temp}>(this page is still a work in progress)</p>

              </div>
            </div>
          </div>
      </React.Fragment>
    );
  }
}

const projectHeader = {
  padding: "1.2em 0em",
  fontSize: "calc( 40px + (50 - 40) * (100vw - 400px)/(800-400) )",
};

const temp = {
  fontSize: "calc( 22px + (25 - 22) * (100vw - 400px)/(800-400) )"
}

export default Projects;
