import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import { faLinkedin } from "@fortawesome/free-brands-svg-icons";

export default class Intro extends Component {

    toAbout = () => {
        this.props.toAbout()
    }

  render() {
    return (
      <React.Fragment>
        <div className="container-custom">
          <div className="row h-100 w-100">
            <div className="col-10 offset-1 col-sm-10 offset-1 col-md-6 offset-md-3 my-auto">
              <h1 style={mainText}>Hi.</h1>
              <p style={introText}>
                I'm Dan Jiang, a Software Engineering student.
              </p>
              <p style={welcomeText}>Welcome to my site.</p>
              <a
                href="https://www.linkedin.com/in/danj/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <FontAwesomeIcon
                  icon={faLinkedin}
                  size="3x"
                  style={iconStyle}
                />
              </a>
              <a
                href="https://github.com/cabbage-py"
                target="_blank"
                rel="noopener noreferrer"
              >
                <FontAwesomeIcon icon={faGithub} size="3x" style={iconStyle} />
              </a>
              <br></br>
              <button
                className="button"
                style={buttonStyle}
                onClick={this.toAbout}
              >
                <span>My Projects</span>
              </button>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const mainText = {
  fontSize: "calc( 100px + (150 - 100) * (100vw - 400px)/(800-400) )",
  textAlign: "left",
  marginLeft: "-0.07em",
  animation: "fadein1 5s",
};

const introText = {
  textAlign: "left",
  fontSize: "calc( 20px + (25 - 20) * (100vw - 400px)/(800-400) )",
  animation: "fadein2 5s",
};

const welcomeText = {
  textAlign: "left",
  fontSize: "calc( 22px + (28 - 22) * (100vw - 400px)/(800-400) )",
  fontWeight: "bold",
  animation: "fadein3 5s",
};

const buttonStyle = {
  fontSize: "calc( 15px + (17 - 15) * (100vw - 400px)/(800-400) )",
  animation: "fadein4 5s",
  border: "none",
  outline: "none"
};

const iconStyle = {
  float: "left",
  padding: "5px",
  animation: "fadein4 5s",
};
