import React, { Component } from "react";
import "./App.css";
import { BrowserRouter as Router, Route } from "react-router-dom";
import BIRDS from 'vanta/dist/vanta.birds.min';
import Header from "./components/Header";
import Main from "./components/Main";
import Projects from "./components/Projects";

import { ThemeProvider } from 'styled-components';
import { lightTheme, darkTheme } from './theme';
import { GlobalStyles } from './global';


export class App extends Component {
  constructor() {
    super()
    this.vantaRef = React.createRef()
    this.state = {
      theme: "light"
    };
  }

  componentDidMount() {
    this.vantaEffect = BIRDS({
      el: this.vantaRef.current,
      color1: 0xffc3f1,
      color2: 0xb837ff,
      colorMode: "lerpGradient",
      separation: 50.00,
      quantity: 4.00,
      backgroundColor: this.state.theme === 'light' ? 0xffffff : 0x90909,
      wingSpan: 1,
      speedLimit: 4
    })
  }

  componentWillUnmount() {
    if (this.vantaEffect) this.vantaEffect.destroy()
  }


  // App.js
  state = {
    theme: "light"
  };

  // The function that toggles between themes
  toggleTheme = () => {
    // if the theme is not light, then set it to dark
    if (this.state.theme === 'light') {
      this.setState({ theme: "dark" });
      this.vantaEffect.setOptions({backgroundColor: 0x90909});
    // otherwise, it should be light
    } else {
      this.setState({ theme: "light" });
      this.vantaEffect.setOptions({backgroundColor: 0xffffff});
    }
  }

  render() {
    return (
      <Router>
        <ThemeProvider theme={this.state.theme === 'light' ? lightTheme : darkTheme}>
          <GlobalStyles/>
          <div ref={this.vantaRef} className="App">
            <Header toggleTheme={this.toggleTheme}/>
            <Route exact path="/feather" component={Main} />
            <Route path="/feather/projects" component={Projects} />
          </div>
        </ThemeProvider>
    </Router>
    )
  }
}

export default App
